import { HttpClientModule } from '@angular/common/http';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { BrowserModule } from '@angular/platform-browser';
import { TableModule } from 'primeng/table';
import { TabViewModule } from 'primeng/tabview';
import { AppComponent } from './app.component';
import { ExchangeViewComponent } from './exchange-view.component';
import { ExchangesListComponent } from './ExchangesList/exchanges-list.component';
import { FinancialService } from './financial.service';
import { StockDetailsComponent } from './StockDetails/stock-details.component';
import { StockListComponent } from './StockList/stock-list.component';
import { ChartModule } from 'primeng/chart';
import { MessageService } from 'primeng/api';
import { CalendarModule } from 'primeng/calendar';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { SelectButtonModule } from 'primeng/selectbutton';
import { AutoCompleteModule } from 'primeng/autocomplete';

@NgModule({
  declarations: [
    AppComponent,
    ExchangesListComponent,
    StockListComponent,
    ExchangeViewComponent,
    StockDetailsComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    FormsModule,
    TableModule,
    TabViewModule,
    ChartModule,
    CalendarModule,
    BrowserAnimationsModule,
    SelectButtonModule,
    AutoCompleteModule
  ],
  providers: [FinancialService, MessageService],
  bootstrap: [AppComponent]
})
export class AppModule { }
