import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';


@Injectable({
  providedIn: 'root'
})
export class FinancialService {
  constructor(private httpClient: HttpClient) { }

  getAvailableExchanges(): Promise<[{ code: string, currency: string, name: string }]> {
    console.log('getAvailableExchanges()');

    try {
      return this.httpClient.get<[{ code: string, currency: string, name: string }]>(
              'https://finnhub.io/api/v1/stock/exchange?token=bqlvdjfrh5rf7sg5u5og')
                .toPromise()
                 .catch(this.handleError);
    }
    catch (error) {
      console.log('an error occured: ' + error);
    }
  }

  getStocksByExchangeCode(exchangeCode: string):
    Promise<[{ description: string, displaySymbol: string, symbol: string }]> {
    console.log('getStocksByExchangeCode() >> exchangeCode=' + exchangeCode);

    try {
      if (exchangeCode === 'indices') {
        return this.httpClient.get<[{ description: string, displaySymbol: string, symbol: string }]>(
                'https://financialmodelingprep.com/api/v3/quotes/index')
                  .toPromise()
                    .catch(this.handleError);
      }
      else {
        return this.httpClient.get<[{ description: string, displaySymbol: string, symbol: string }]>(
                'https://finnhub.io/api/v1/stock/symbol?exchange=' + exchangeCode + '&token=bqlvdjfrh5rf7sg5u5og')
                  .toPromise()
                    .catch(this.handleError);
      }
    }
    catch (error) {
      console.log('an error occured: ' + error);
    }
  }

  getStockQuoteBySymbol(stockSymbol: string):
    Promise<{ c: string, h: string, l: string, o: string, pc: string, t: string }> {
    console.log('getStockQuoteBySymbol() >> stockSymbol=' + stockSymbol);

    try {
      return this.httpClient.get<{ c: string, h: string, l: string, o: string, pc: string, t: string }>(
              'https://finnhub.io/api/v1/quote?symbol=' + stockSymbol + '&token=bqlvdjfrh5rf7sg5u5og')
                .toPromise()
                  .catch(this.handleError);
    }
    catch (error) {
      console.log('an error occured: ' + error);
    }
  }

  getStockCandlesBySymbol(stockSymbol: string, resolution: string, from: number, to: number): Promise<any> {

    console.log('getStockCandlesBySymbol() >> stockSymbol=' + stockSymbol + ' resolution=' + resolution + ' from=' + from + ' to=' + to);

    try {
    return this
            .httpClient
              .get<any>(
                'https://finnhub.io/api/v1/stock/candle?symbol=' + stockSymbol +
                '&resolution=' + resolution +
                '&from=' + from +
                '&to=' + to +
                '&token=bqlvdjfrh5rf7sg5u5og')
                  .toPromise()
                    .catch(this.handleError);
    }
    catch (error) {
      console.log('an error occured: ' + error);
    }
  }

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error);
    return Promise.reject(error.message || error);
}
}
